import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
	{
		path: '/',
		name: 'Index',
		component: () => import('@/views/index'),
		meta: {
			title: ''
		}
	},
	{
		path: '/ido',
		name: 'Ido',
		component: () => import('@/views/ido'),
		meta: {
			title: ''
		}
	},
	{
		path: '/share',
		name: 'Share',
		component: () => import('@/views/share'),
		meta: {
			title: ''
		}
	},
	{
		path: '/top',
		name: 'Top',
		component: () => import('@/views/top'),
		meta: {
			title: ''
		}
	},
	{
		path: '/account',
		name: 'Account',
		component: () => import('@/views/account'),
		meta: {
			title: ''
		}
	},
	{
		path: '/swap',
		name: 'Swap',
		component: () => import('@/views/swap'),
		meta: {
			title: ''
		}
	},
	{
		path: '/team',
		name: 'Team',
		component: () => import('@/views/team'),
		meta: {
			title: ''
		}
	},
	{
		path: '/fund',
		name: 'Fund',
		component: () => import('@/views/fund'),
		meta: {
			title: ''
		}
	},
	{
		path: '/nft',
		name: 'Nft',
		component: () => import('@/views/nft'),
		meta: {
			title: ''
		}
	},
	{
		path: '/calculator',
		name: 'Calculator',
		component: () => import('@/views/calculator'),
		meta: {
			title: ''
		}
	},
	{
		path: '/reward',
		name: 'Reward',
		component: () => import('@/views/reward'),
		meta: {
			title: ''
		}
	}
];

const router = createRouter({
	history: createWebHashHistory(),
	routes
});

router.beforeEach((to) => {
	if (to.meta.title) {
		document.title = to.meta.title;
	}
});

export default router;
